.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.home .wrapper {
  padding-top: 0;
  padding-bottom: 0;
}

body {
  overflow-x: hidden;
  font-variation-settings: "ital" 0, "wght" 400, "wdth" 100;
}

.sticky-content {
  position: sticky;
  top: 0;
}

.with-line {
  position: relative;
}
.with-line::after {
  content: "";
  position: absolute;
  top: 25px;
  left: 25px;
  width: 97%;
  border-bottom: 2px solid;
}
.with-line.line-white::after {
  border-color: #fff;
}
.with-line.line-black::after {
  border-color: #232323;
}
.with-line.line-primary::after {
  border-color: #CB6959;
}

.centered {
  position: absolute;
  top: 25%;
  left: 15%;
  transform: translate(6%, 3%);
}

form [type=text],
form [type=email],
form textarea {
  border: none !important;
  border-bottom: 1px solid #CB6959 !important;
  border-radius: 0 !important;
  margin-bottom: 38px !important;
}
form label {
  position: absolute !important;
  top: 28px;
}
form .wp-forms-field {
  position: relative;
}
.section {
  padding-top: 99px;
  padding-bottom: 99px;
}

.bordered-section {
  border-top: 1px solid #CB6959;
  border-bottom: 1px solid #CB6959;
  padding-top: 77px;
  padding-bottom: 62px;
}

.section--home-video-cta {
  z-index: 100;
}
.section--home-video-cta video {
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: 0;
  margin-bottom: -4px;
}

.section--decarbonise {
  height: 1206px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.section--decarbonise::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%);
}
.section--decarbonise video {
  top: 0;
  left: 0;
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.section--with-divider {
  background: linear-gradient(to left, #232323 50%, #fff 61%);
  z-index: -1;
  margin-bottom: -1px;
}

.section--concept {
  background: linear-gradient(to left, #fff 61%, #F7F2E5 50%);
}
.section--concept .content-container {
  width: 35%;
}
.section--concept .content-container-bottom {
  height: 100%;
}
.section--concept .content {
  padding-top: 135px;
  padding-left: 0;
  padding-right: 0;
}
.section--concept .img-concept {
  position: absolute;
  top: 15%;
  left: 10%;
  z-index: 100;
}

.vertical-link-container {
  height: 682px;
}
.vertical-link-container .vertical-link {
  transform: rotate(270deg);
  position: absolute;
  left: -15px;
  bottom: 60px;
  transform-origin: 0 0;
}
.vertical-link-container .vertical-link a {
  text-decoration: none;
  font-size: 16px;
  font-style: normal;
  line-height: normal;
  letter-spacing: 1.6px;
  text-transform: uppercase;
  font-variation-settings: "ital" 0, "wght" 700, "wdth" 120;
}
.vertical-link-container .vertical-link-lg {
  transform: rotate(270deg);
  position: absolute;
  text-align: center;
  font-size: 28px;
  font-style: normal;
  line-height: 110%; /* 30.8px */
  text-transform: uppercase;
  text-decoration: none;
}
.vertical-link-container .vertical-link-lg a {
  font-variation-settings: "ital" 0, "wght" 700, "wdth" 125;
  text-decoration: none;
}
.vertical-link-container .vertical-link-lg.vl-1 {
  bottom: 204px;
  left: 175px;
}
.vertical-link-container .vertical-link-lg.vl-2 {
  bottom: 260px;
  left: 330px;
}
.vertical-link-container .vertical-link-lg.vl-3 {
  bottom: 238px;
  right: -13%;
}
.vertical-link-container .vertical-links {
  flex-basis: 60%;
  position: relative;
}

.section--vision {
  background: #CB6959;
  padding-bottom: 255px;
  position: relative;
}
.section--vision::before {
  content: "";
  background: url(../img/vision-section.png) no-repeat;
  background-size: cover;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  transform: translateY(-40%);
}

.section--call-to-action {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section--call-to-action .content {
  position: absolute;
  top: 50%;
  /* left: 0; */
  transform: translateY(-50%);
  margin: auto;
}

.section--content {
  padding: 130px 0;
}
.section--content .h4-title {
  font-size: 36px;
  font-style: normal;
  line-height: 120%; /* 43.2px */
  font-variation-settings: "ital" 0, "wght" 500, "wdth" 100;
  margin-bottom: 38px;
}
.section--content .content {
  font-size: 24px;
  font-style: normal;
  font-variation-settings: "ital" 0, "wght" 300, "wdth" 100;
  line-height: 1.6;
  margin-bottom: 38px;
}

.section--large-content {
  padding: 122px 0;
  background: #CB6959;
}
.section--large-content .main-title {
  font-size: 60px;
  font-style: normal;
  line-height: 110%; /* 66px */
  font-variation-settings: "ital" 0, "wght" 800, "wdth" 125;
  margin-bottom: 0;
}
.section--large-content .headline {
  font-size: 40px;
  font-style: normal;
  line-height: 120%;
  font-variation-settings: "ital" 0, "wght" 700, "wdth" 125;
}
.section--large-content .headline-body {
  /* Body Large */
  font-size: 24px;
  font-style: normal;
  font-variation-settings: "ital" 0, "wght" 300, "wdth" 100;
  line-height: normal;
  line-height: 1.6;
}
.section--large-content .article-body {
  font-size: 20px;
  font-style: normal;
}
.section--large-content .article-content {
  font-variation-settings: "ital" 0, "wght" 300, "wdth" 100;
  line-height: 1.6;
}
.section--large-content .bordered-content {
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: #232323;
  height: 100%;
}
.section--large-content .bordered-content.border-white {
  border-color: #fff;
}

.section--jumbotron {
  height: 736px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.section--dates {
  padding-top: 67px;
  padding-bottom: 138px;
}
.section--dates-with-bg {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background: url("../img/date-background.png");
}
.section--dates-lg {
  padding-top: 110px;
  padding-bottom: 205px;
}

.section--contact {
  padding-top: 100px;
  padding-bottom: 135px;
}

