  left: 44px;
}
.custom-logo-link:hover .custom-logo-d {
  left: 74px;
}
.custom-logo-link:hover .custom-logo-e {
  left: 105px;
}
.custom-logo-link:hover .custom-logo-l {
  left: 135px;
}
.navbar-nav .nav-item {
  margin-right: 37px;
}
.navbar-nav .nav-item:last-child {
  margin-right: 0;
}
.navbar-nav .nav-item .nav-link {
  font-size: 16px;
  padding: 0;
  border-bottom: 1px solid transparent;
}
.navbar-nav .nav-item .nav-link:hover {
  border-bottom-color: #F7F2E5;
}

.home .navbar-nav .nav-item .nav-link {
