}
.footer-main-menu-container .navbar-nav {
  width: 100%;
}
.footer-main-menu-container .navbar-nav .nav-item {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #CB6959;
  margin-right: 0;
}
.footer-main-menu-container .navbar-nav .nav-item .nav-link {
  color: #CB6959;
  font-size: 16px;
  font-style: normal;
  line-height: normal;
  text-transform: uppercase;
  font-variation-settings: "wght" 400, "wdth" 130;
}

#wrapper-navbar {
  position: fixed;
  width: 100%;
  z-index: 1030;
}
.btn, .wpcf7 input[type=submit], .woocommerce-notices-wrapper .woocommerce-message a.button, .woocommerce-info a.button, form [type=submit] {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.7px;
}
.btn svg, .wpcf7 input[type=submit] svg, .woocommerce-notices-wrapper .woocommerce-message a.button svg, .woocommerce-info a.button svg, form [type=submit] svg {
  margin-top: 0px;
  margin-left: 20px;
}

.btn-outline-primary:hover, .wpcf7 input[type=submit]:hover, form [type=submit]:hover {
  color: #fff;
}
.btn-outline-primary:hover svg path, .wpcf7 input[type=submit]:hover svg path, form [type=submit]:hover svg path {
  fill: #fff;
}
.btn-outline-primary svg path, .wpcf7 input[type=submit] svg path, form [type=submit] svg path {
  fill: #CB6959;
}

.btn-outline-light:hover {
  color: #232323;
}
.btn-outline-light:hover svg path {
  fill: #232323;
}
.btn-outline-light svg path {
  fill: #fff;
}

.bg-hoverable {
  width: 100vw;
}
.img-greyscale-hoverable,
.img-hoverable {
  cursor: pointer;
  filter: grayscale(1);
  transition: 0.3s filter;
}
.img-greyscale-hoverable:hover,
.img-hoverable:hover {
  filter: grayscale(0);
}

.img-black-hoverable {
  cursor: pointer;
  filter: brightness(0) saturate(0%);
  transition: 0.3s filter;
}
.img-black-hoverable:hover {
  filter: brightness(1) saturate(1);
}

.section--home-hero {
  background-color: #CB6959;
